/* Navbar container */
.navbar {
  background-color: #f2f2f2;
  padding: 10px;
}

/* Navbar list */
.navbar-nav {
  display: flex;
  list-style-type: none;
  margin: 0;
  padding: 0;
}

/* Navbar list items */
.nav-item {
  margin-right: 10px;
}

/* Navbar links */
.nav-link {
  color: #333;
  text-decoration: none;
  padding: 5px 10px;
}

/* Navbar link hover */
.nav-link:hover {
  background-color: #ddd;
}
